.banner-header{position:relative}@media (max-width:767px){.banner-header{height:496px}}.banner-header__image{position:absolute;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.slider-banner__wrapper{position:relative}.slider-banner__item{position:relative;height:calc(100vh - 112px)}@media (max-width:767px){.slider-banner__item{height:496px}}.slider-banner__image,.slider-banner__video{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.slider-banner__nav{position:absolute;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);width:40px;height:40px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;cursor:pointer}.slider-banner__nav--prev{left:20px}.slider-banner__nav--prev svg{-webkit-transform:scale(-1);-ms-transform:scale(-1);transform:scale(-1)}.slider-banner__nav--next{right:20px}