.materials__border{border-top:1px solid #e6e6e6}.materials__offset-bottom{padding-bottom:30px}@media (min-width:540px){.materials__offset-bottom{padding-bottom:calc(30px + (6000vw - 32400px)/484)}}@media (min-width:1024px){.materials__offset-bottom{padding-bottom:90px}}@media (max-width:991px){.materials__item{margin-bottom:40px}}.materials__title{min-height:230px;padding-bottom:20px;border-bottom:3px solid #413c2d}.materials__title--small-offset{min-height:110px}@media (max-width:991px){.materials__title{min-height:130px}}@media (max-width:767px){.materials__title{min-height:auto}}.materials__title h3{font-family:SmilePlasticsStencil-Regular,sans-serif;color:#413c2d;font-size:26px}@media (min-width:540px){.materials__title h3{font-size:calc(26px + (2000vw - 10800px)/484)}}@media (min-width:1024px){.materials__title h3{font-size:46px}}.materials__title h3{line-height:34px}@media (min-width:540px){.materials__title h3{line-height:calc(34px + (2200vw - 11880px)/484)}}@media (min-width:1024px){.materials__title h3{line-height:56px}}.materials__description{padding-top:25px}.materials__list{font-size:22px;line-height:29px;letter-spacing:-1px}@media (max-width:767px){.materials__list{font-size:18px;line-height:25px}}.materials__list li:not(:last-child){margin-bottom:35px}@media (max-width:991px){.materials__list li:not(:last-child){margin-bottom:20px}}.materials__list span{display:block}.materials__list span>b{display:inline-block}.materials__list b{font-family:UntitledSans-Medium,sans-serif;font-weight:400;display:block}