60px}@media (min-width:540px){.section--offset-equally{padding-bottom:calc(60px + (6000vw - 32400px)/451)}}@media (min-width:991px){.section--offset-equally{padding-bottom:120px}}.section--border-top{border-top:1px solid #e6e6e6}.section-gray{min-height:320px;padding:24px 0;background-color:#413c2d}