button{border:0}.button,button{outline:0;cursor:pointer}.button{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;text-align:center;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.button--white{min-height:60px;padding:15px 30px 18px;color:#413c2d;border-radius:3px;background-color:#fff;border:1px solid #fff;font-family:UntitledSans-Medium,sans-serif;font-size:22px;line-height:1.27;letter-spacing:-1px}@media (max-width:767px){.button--white{font-size:18px;line-height:24px;padding:11px 20px 13px;min-height:48px}}.button--white:hover{color:#fff;background-color:#413c2d;border:1px solid #fff}.button--gray{min-height:60px;padding:15px 30px 18px;color:#fff!important;border-radius:3px;background-color:#413c2d;border:1px solid #413c2d;font-family:UntitledSans-Medium,sans-serif;font-size:22px;line-height:1.27;letter-spacing:-1px}@media (max-width:767px){.button--gray{font-size:18px;line-height:24px;padding:11px 20px 13px;min-height:48px}}.button--gray:hover{color:#413c2d!important;background-color:rgba(0,0,0,0)}