.material-content{border-bottom:1px solid #e6e6e6}.material-content--offset-bottom{padding-bottom:60px}@media (min-width:480px){.material-content--offset-bottom{padding-bottom:calc(60px + (6800vw - 32640px)/511)}}@media (min-width:991px){.material-content--offset-bottom{padding-bottom:128px}}.material-content__image,.material-content__text{height:100%;width:100%}.material-content__image{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.material-content__image img{max-width:100%}.material-content__text p,.material-content__text ul{margin-bottom:22px;min-height:1px}@media (max-width:767px){.material-content__text p,.material-content__text ul{margin-bottom:14px}}