.product-preview{padding-top:36px}.product-preview__image{font-size:0}.product-preview__image a{position:relative;display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex}.product-preview__image img{max-width:100%}.product-preview__name{margin-bottom:22px}@media (min-width:540px){.product-preview__name{margin-bottom:calc(22px + (700vw - 3780px)/451)}}@media (min-width:991px){.product-preview__name{margin-bottom:29px}}.product-preview__items h4,.product-preview__name h3{display:block;font-family:UntitledSans-Medium,sans-serif;font-weight:400;font-size:22px;line-height:29px;letter-spacing:-1px}.product-preview__items h4{margin-bottom:10px}.product-preview__items:not(:last-child){margin-bottom:24px}@media (min-width:540px){.product-preview__items:not(:last-child){margin-bottom:calc(24px + (800vw - 4320px)/451)}}@media (min-width:991px){.product-preview__items:not(:last-child){margin-bottom:32px}}.product-preview__button{margin-top:32px}@media (min-width:540px){.product-preview__button{margin-top:calc(32px + (1600vw - 8640px)/451)}}@media (min-width:991px){.product-preview__button{margin-top:48px}}