.cookie{position:fixed;bottom:32px;right:32px;max-width:560px;width:100%;padding:24px 32px;background-color:#413c2d;color:#fff}@media (max-width:600px){.cookie{right:15px;bottom:15px;max-width:414px}}@media (max-width:480px){.cookie{right:0;bottom:0;max-width:414px}}.cookie__contents a{position:relative;color:#fff}.cookie__contents a:hover:before{opacity:0;visibility:hidden}.cookie__contents a:before{position:absolute;bottom:0;left:0;content:"";width:100%;height:1px;background:#fff}.cookie__contents a:before,.cookie__link{-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.cookie__link{display:inline-block;color:#fff;margin-top:32px}.cookie__link:hover{opacity:.7}.cookie__link svg{fill:#fff;margin-right:10px}