.catalog--border-top{border-top:1px solid #e6e6e6}.catalog--offset-big{padding-top:40px}@media (min-width:540px){.catalog--offset-big{padding-top:calc(40px + (6000vw - 32400px)/451)}}@media (min-width:991px){.catalog--offset-big{padding-top:100px}}.catalog__head{font-family:UntitledSans-Medium,sans-serif;letter-spacing:.02px;color:#000;text-transform:uppercase;padding-top:8px;font-size:20px}@media (min-width:540px){.catalog__head{font-size:calc(20px + (600vw - 3240px)/484)}}@media (min-width:1024px){.catalog__head{font-size:26px}}.catalog__head{line-height:26px}@media (min-width:540px){.catalog__head{line-height:calc(26px + (600vw - 3240px)/484)}}@media (min-width:1024px){.catalog__head{line-height:32px}}.catalog__head{margin-bottom:30px}@media (min-width:540px){.catalog__head{margin-bottom:calc(30px + (5500vw - 29700px)/484)}}@media (min-width:1024px){.catalog__head{margin-bottom:85px}}.catalog__item{max-width:360px;margin:0 auto 28px}.catalog__image-wrapper{position:relative;max-width:360px;font-size:0}.catalog__image-wrapper:before{content:"";padding-top:100%;display:block}.catalog__image{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}