.your-design{background:#413c2d;min-height:408px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:58px 0 64px}@media (max-width:767px){.your-design{min-height:auto}}.your-design--offset{margin-bottom:32px}.your-design__conent{text-align:center;color:#fff}.your-design__conent h3{font-family:UntitledSans-Medium,sans-serif;font-weight:500;letter-spacing:-1.12px;margin-bottom:4px;font-size:20px}@media (min-width:570px){.your-design__conent h3{font-size:calc(20px + (600vw - 3420px)/454)}}@media (min-width:1024px){.your-design__conent h3{font-size:26px}}.your-design__conent h3{line-height:26px}@media (min-width:480px){.your-design__conent h3{line-height:calc(26px + (600vw - 2880px)/1320)}}@media (min-width:1800px){.your-design__conent h3{line-height:32px}}.your-design__conent p{letter-spacing:-1px;font-size:18px}@media (min-width:570px){.your-design__conent p{font-size:calc(18px + (400vw - 2280px)/454)}}@media (min-width:1024px){.your-design__conent p{font-size:22px}}.your-design__conent p{line-height:24px}@media (min-width:480px){.your-design__conent p{line-height:calc(24px + (500vw - 2400px)/1320)}}@media (min-width:1800px){.your-design__conent p{line-height:29px}}.your-design__button{margin-top:26px;text-align:center}